export default ()=> {
      return (
        <div className="question" data-index="49" dangerouslySetInnerHTML = {{ __html: `
<h6>49. <code>num</code>的值是什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> num <span class="token operator">=</span> <span class="token function">parseInt</span><span class="token punctuation">(</span><span class="token string">"7*6"</span><span class="token punctuation">,</span> <span class="token number">10</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>42</code></li>
<li>B: <code>&quot;42&quot;</code></li>
<li>C: <code>7</code></li>
<li>D: <code>NaN</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: C</h4>
<p>只返回了字符串中第一个字母. 设定了 <em>进制</em> 后 (也就是第二个参数，指定需要解析的数字是什么进制: 十进制、十六机制、八进制、二进制等等……),<code>parseInt</code> 检查字符串中的字符是否合法. 一旦遇到一个在指定进制中不合法的字符后，立即停止解析并且忽略后面所有的字符。</p>
<p><code>*</code>就是不合法的数字字符。所以只解析到<code>&quot;7&quot;</code>，并将其解析为十进制的<code>7</code>. <code>num</code>的值即为<code>7</code>.</p>
</p>
</details>
` }}></div>
      );
    }